import { BlockMock, DeepPartial } from '../core'
import { VaultBlockTvlMock } from '../vault-blocks'
import { WEB3_DEFAULT_ADDR } from '../web3-client'
import {
  VaultEpoch,
  VaultEpochAPRs,
  VaultEpochQueue,
  VaultEpochInstantWithdraws,
  VaultEpochWithdraws,
  VaultCdoEpochData,
  VaultEpochAPYs,
  VaultCreditExtended,
  VaultEpochInterest,
} from './vault-epoch.model'

/**
 * Vault Epoch Mock
 */
export function VaultEpochMock(options?: DeepPartial<VaultEpoch>): VaultEpoch {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'VAULT_EPOCH_ID',

    block: BlockMock(options?.block),
    vaultId: options?.vaultId || 'VAULT_ID',
    vaultAddress: options?.vaultAddress || WEB3_DEFAULT_ADDR,

    APRs: VaultEpochAPRsMock(options?.APRs),
    APYs: VaultEpochAPYsMock(options?.APYs),

    totalSupply: options?.totalSupply || '0',
    price: options?.price || '0',
    TVL: VaultBlockTvlMock(options?.TVL),
    creditExtended: VaultCreditExtendedMock(options?.creditExtended),

    interest: VaultEpochInterestMock(options?.interest),
    expectedInterest: options?.expectedInterest || '0',

    deposits: options?.deposits || '0',
    duration: options?.duration || 0,
    bufferDuration: options?.bufferDuration || 0,
    unclaimedFees: options?.unclaimedFees || '0',

    startDate: options?.startDate,
    endDate: options?.endDate,
    count: options?.count || 0,
    status: options?.status || 'WAITING',
    depositQueue: VaultCdoEpochDepositQueueMock(options?.depositQueue),

    withdrawType: options?.withdrawType || 'STANDARD',

    withdraws: options?.withdraws
      ? VaultEpochWithdrawsMock(options.withdraws)
      : undefined,
    instantWithdraws: options?.instantWithdraws
      ? VaultEpochInstantWithdrawsMock(options.instantWithdraws)
      : undefined,

    createdAt: options?.createdAt || now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: options?.updatedAt || now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}

export function VaultCreditExtendedMock(
  options?: Partial<VaultCreditExtended>
): VaultCreditExtended {
  return {
    token: options?.token || '0',
    USD: options?.USD || '0',
  }
}

export function VaultCdoEpochDepositQueueMock(
  options?: Partial<VaultEpochQueue>
): VaultEpochQueue {
  return {
    status: options?.status || 'PENDING',
    amount: options?.amount || '0',
  }
}

export function VaultEpochAPRsMock(
  options?: Partial<VaultEpochAPRs>
): VaultEpochAPRs {
  return {
    NET: options?.NET || 0,
    DELTA: options?.DELTA || 0,
    GROSS: options?.GROSS || 0,
    EPOCH: options?.EPOCH || 0,
    BUFFER: options?.BUFFER || 0,
    CURE: options?.CURE || 0,
  }
}

export function VaultEpochAPYsMock(
  options?: Partial<VaultEpochAPYs>
): VaultEpochAPYs {
  return {
    NET: options?.NET || 0,
    GROSS: options?.GROSS || 0,
    FEE: options?.FEE || 0,
  }
}

export function VaultEpochInterestMock(
  options?: Partial<VaultEpochInterest>
): VaultEpochInterest {
  return {
    NET: options?.NET || '0',
    GROSS: options?.GROSS || '0',
    FEE: options?.FEE || '0',
  }
}

export function VaultEpochWithdrawsMock(
  options?: Partial<VaultEpochWithdraws>
): VaultEpochWithdraws {
  return {
    amount: options?.amount || '0',
    fees: options?.fees || '0',
  }
}

export function VaultEpochInstantWithdrawsMock(
  options?: Partial<VaultEpochInstantWithdraws>
): VaultEpochInstantWithdraws {
  return {
    amount: options?.amount || '0',
    delay: options?.delay || 0,
    aprDelta: options?.aprDelta || 0,
    allowed: options?.allowed ?? true,
    deadline: options?.deadline,
  }
}

export function VaultCdoEpochDataMock(
  options?: Partial<VaultCdoEpochData>
): VaultCdoEpochData {
  return {
    epoch: VaultEpochMock(options?.epoch),
    lastEpoch: VaultEpochMock(options?.lastEpoch),
    withdrawAmount: options?.withdrawAmount || '0',
    withdrawDeadline: options?.withdrawDeadline || new Date(),
    instantWithdrawed: !!options?.instantWithdrawed,
    interests: options?.interests || '0',
    duration: options?.duration || 0,
    durationSeconds: options?.durationSeconds || 0,
    unit: options?.unit || 'seconds',
    APRs: VaultEpochAPRsMock(options?.APRs),
    feePercentage: options?.feePercentage || 0,
    toWithdraw: options?.toWithdraw || '0',
    isDefaultable: !!options?.isDefaultable,
  }
}
